			this.showPayPasswordPopup = true;
		},
		//关闭验证密码
		closePayPassword() {
			this.showPayPasswordPopup = false
			this.payPw = '';

		},
		// 数字键盘 事件
		onInput(key) {
			let that = this
			this.payPw = (this.payPw + key).slice(0, 6);
			if (this.payPw.length == 6) {
				
				let option = {
                    pay_password: this.payPw,
					token: this.user_info.token,
					money: this.moneyTemp,
					type_id:this.guess_type
                }
                this.utils.showLoading();
                this.closePayPassword();
                this.http.post(this.http._HOST + 'wave/buy',option).then(result =>{
                    this.utils.info('猜涨跌...',result); 
                    this.utils.dismissLoading(); 
                    if(result.success){
						
						this.utils.showToast('竞猜成功');

						//刷新数据
						this.getUserInfo('1');
                       
                    }else{
                        this.utils.showAlert(result.message);
                    }
                })
				
			}
		},
		onDelete() {
			this.payPw = this.payPw.slice(0, this.payPw.length - 1);
		},
