				this.utils.info('获取K线图信息...',result);
				
				let btn_data = result.data;
				for(let i=0;i<btn_data.length;i++){
                    btn_data[i][0] = this.utils.timeFormat( btn_data[i][0]);
				}

				var data0 = this.splitData(btn_data);

				this.btn_k_data = data0;


				// console.log('data0...',data0);
				// console.log('calculateMA5...',this.calculateMA(data0,5));
				
				if(this.chartline){
					console.log('k线图数据更新...');
					this.chartline.hideLoading();

					// let option = {
					// 	xAxis: {
					// 		data: data0.categoryData,	
					// 	},
					// 	series: [
					// 		{
					// 			data: data0.values,
					// 		},
					// 		{
					// 			data: this.calculateMA(data0,5),
					// 		},
					// 		{
					// 			data: this.calculateMA(data0,10),
					// 		},
					// 		{
					// 			data: this.calculateMA(data0,20),
					// 		},

					// 	]
					// }
					var upColor = '#ec0000';
					var upBorderColor = '#8A0000';
					var downColor = '#00da3c';
					var downBorderColor = '#008F28';

					let option = {
						tooltip: {
							trigger: 'axis',
							axisPointer: {
								type: 'cross'
							}
						},
						legend: {
							data: ['日K', 'MA5', 'MA10', 'MA20',],
							selectedMode:false,
							left:0,
							top:0,
							itemGap:10
						},
						grid: {
							left: '40',
							right: '0',
							bottom: '30',
							top:'40'
						},
						xAxis: {
							type: 'category',
							data: data0.categoryData,	
							scale: true,
							boundaryGap : false,
							axisLine: {onZero: false},
							splitLine: {show: false},
							splitNumber: 30,
							min: 'dataMin',
							max: 'dataMax'
						},
						yAxis: {
							scale: true,
							splitArea: {
								show: true
							}
						},
						dataZoom: [
							{
								type: 'inside',
								start: 50,
								end: 100
							},
						],
						series: [
							{
								name: '日K',
								type: 'candlestick',
								data: data0.values,
								itemStyle: {
									normal: {
										color: upColor,
										color0: downColor,
										borderColor: upBorderColor,
										borderColor0: downBorderColor
									}
								},
							
							},
							{
								name: 'MA5',
								type: 'line',
								data: this.calculateMA(data0,5),
								smooth: true,
								lineStyle: {
									normal: {opacity: 0.5}
								}
							},
							{
								name: 'MA10',
								type: 'line',
								data: this.calculateMA(data0,10),
								smooth: true,
								lineStyle: {
									normal: {opacity: 0.5}
								}
							},
							{
								name: 'MA20',
								type: 'line',
								data: this.calculateMA(data0,20),
								smooth: true,
								lineStyle: {
									normal: {opacity: 0.5}
								}
							},

						]
					}
					
					this.chartline.setOption(option);
				}

				
			}).catch(err =>{
				this.utils.info('获取K线图信息err...',err);

			})
			
		},
		//处理k线图数据
		splitData(rawData) {
			var categoryData = [];
			var values = []
			for (var i = 0; i < rawData.length; i++) {
				categoryData.push(rawData[i].splice(0, 1)[0]);
				values.push(rawData[i])
			}
			return {
				categoryData: categoryData,
				values: values
			};
		},
		//均线数据处理
		calculateMA(data0,dayCount) {
			var result = [];
			for (var i = 0, len = data0.values.length; i < len; i++) {
				if (i < dayCount) {
					result.push('-');
					continue;
				}
				var sum = 0;
				for (var j = 0; j < dayCount; j++) {
					sum += data0.values[i - j][1];
				}
				result.push((sum / dayCount).toFixed(4));
			}
			return result;
		},
		//获取用户信息 type 1 去请求btn信息 2不去
		getUserInfo(type){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('获取用户信息...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_money;
                    this.user_info.pay_password = data.pay_password;
					this.user_info.token = data.token;
					
					if(type == '1'){
						//获取进去btn信息
						this.getPageInfo();
					}

                }
			})
		},
		//获取页面数据
		getPageInfo(){
			let option = {
                token: this.user_info.token
            }
			this.http.post(this.http._HOST + 'wave/show_btc',option).then(result =>{
				this.utils.info('获取页面数据...',result);
				if(result.success){
					this.page_data = result.data;

					if(this.page_data.risa == 0  && this.page_data.fall == 0){
						this.$refs.homeProgressLeft.style.width = '50%';
					}else{
						this.$refs.homeProgressLeft.style.width = this.page_data.risa + '%';
					}
					

					//先清除定时器
					if(this.timeInterval){
						clearInterval(this.timeInterval);	
					}
					if(this.timeResultInterval){
						clearInterval(this.timeResultInterval);	
					}
					if(this.page_data.user_postbtc){//下注了
						this.page_data.user_postbtc.game_time = parseInt(Math.abs(this.page_data.user_postbtc.game_time)) + 30;
						this.game_time_tmp = parseInt(Math.abs(this.page_data.user_postbtc.game_time)) + 30;

						//时间倒计时
						this.timeResultInterval = setInterval(() =>{
							this.page_data.user_postbtc.game_time --;
							// this.utils.info('购买期数开奖时间...',this.page_data.user_postbtc.game_time);
							if(this.page_data.user_postbtc.game_time == 0){
								this.utils.info('开奖...',);

								//请求开奖结果
								this.getDrawResult(this.page_data.user_postbtc.btc_id);
								
								clearInterval(this.timeResultInterval);

								//再次请求，获取新期数信息
								this.getPageInfo();
							}
						},1000)
					}else{//没下注
						this.page_data.next_time = parseInt(Math.abs(this.page_data.next_time)) + 30;
						this.next_time_tmp = parseInt(Math.abs(this.page_data.next_time)) + 30;
						//时间倒计时
						this.timeInterval = setInterval(() =>{
							this.page_data.next_time --;
							// this.utils.info('当前期数剩余时间...',this.page_data.next_time);
							if(this.page_data.next_time == 0){
								this.utils.info('时间结束，获取新期数信息...',);

								clearInterval(this.timeInterval);

								//再次请求，获取新期数信息
								this.getPageInfo();
							}
						},1000)
					}
					
				}
			})
		},
		//请求开奖结果
		getDrawResult(btc_id){
			let option = {
				btc_id: btc_id,
				token: this.user_info.token
			}

			this.http.post(this.http._HOST + 'wave/new_result',option).then(result =>{
				this.utils.info('获取开奖结果...',result);
				if(result.success){
					
					this.guess_result = result.data;

					this.showGuessResultPopup = true;
				}
			})

		},
		//绘制k线图
		drawKLine(){
			var upColor = '#ec0000';
            var upBorderColor = '#8A0000';
            var downColor = '#00da3c';
			var downBorderColor = '#008F28';

			let option = {
				tooltip: {
					trigger: 'axis',
					axisPointer: {
						type: 'cross'
					}
				},
				legend: {
					data: ['日K', 'MA5', 'MA10', 'MA20',],
					selectedMode:false,
					left:0,
					top:0,
					itemGap:10
				},
				grid: {
					left: '40',
					right: '0',
					bottom: '30',
					top:'40'
				},
				xAxis: {
					type: 'category',
					data: [],
					scale: true,
					boundaryGap : false,
					axisLine: {onZero: false},
					splitLine: {show: false},
					splitNumber: 30,
					min: 'dataMin',
					max: 'dataMax'
				},
				yAxis: {
					scale: true,
					splitArea: {
						show: true
					}
				},
				dataZoom: [
					{
						type: 'inside',
						start: 50,
						end: 100
					},
				],
				series: [
					{
						name: '日K',
						type: 'candlestick',
						data: [],
						itemStyle: {
							normal: {
								color: upColor,
								color0: downColor,
								borderColor: upBorderColor,
								borderColor0: downBorderColor
							}
						},
					
					},
					{
						name: 'MA5',
						type: 'line',
						data: [],
						smooth: true,
						lineStyle: {
							normal: {opacity: 0.5}
						}
					},
					{
						name: 'MA10',
						type: 'line',
						data: [],
						smooth: true,
						lineStyle: {
							normal: {opacity: 0.5}
						}
					},
					{
						name: 'MA20',
						type: 'line',
						data: [],
						smooth: true,
						lineStyle: {
							normal: {opacity: 0.5}
						}
					},

				]
			}
			this.chartline = this.$echarts.init(this.$refs.homeChartContent);
			this.chartline.setOption(option);
		},

		//选择k线图类型
		kTypeChange(index){
			if(this.k_type == index){
				return false;
			}
			this.k_type = index;
			if(this.chartline){
				this.chartline.clear();
			}
			this.getBtnFun(this.k_type,1);
		},
		//头像出错
		getDefaultImg(event){
			let default_user_img = require('../assets/imgs/default-user.png');
			event.target.src = default_user_img;
		},
		//关闭竞猜结果
		closeGuessResult(){
			this.showGuessResultPopup = false;
		},
		//定时器获取数据
		getNewBtnData(){
			this.$jsonp('https://data.block.cc/api/v1/price?symbol_name=bitcoin').then(result =>{
				this.utils.info('获取最新btn信息...',result);
				let new_data = result.data[0];
				let new_btn_data = [];
				// new_btn_data.push(this.utils.timeFormat(new_data.timestamps));
				new_btn_data.push(new_data.price);
				new_btn_data.push(new_data.price);
				new_btn_data.push((new_data.price * (1 - new_data.change_hourly)).toFixed(4));
				new_btn_data.push((new_data.price * (1 + new_data.change_hourly)).toFixed(4));

				

				let category = this.utils.timeFormat(new_data.timestamps);
				
				console.log('new_btn_data...',new_btn_data);
				console.log('category...',category);
				console.log('btn_k_data...1',this.btn_k_data);


				this.btn_k_data.categoryData.shift();
				this.btn_k_data.categoryData.push(category);
				this.btn_k_data.values.shift();
				this.btn_k_data.values.push(new_btn_data);

				console.log('btn_k_data...2',this.btn_k_data);

				
				let data0 = this.btn_k_data;
				
				if(this.chartline){
					
					this.chartline.hideLoading();

					let option = {
						xAxis: {
							data: data0.categoryData,	
						},
						series: [
							{
								data: data0.values,
							},
							{
								data: this.calculateMA(data0,5),
							},
							{
								data: this.calculateMA(data0,10),
							},
							{
								data: this.calculateMA(data0,20),
							},

						]
					}
					this.chartline.setOption(option);
				}
			})
		},
		//测试okcoin api
		okCoinTest(){
			this.$jsonp('https://www.okcoin.com/api/v1/kline.do?symbol=btc_cny&type=1min').then(result =>{
				this.utils.info('okcoin...',result);
			}).catch(err =>{
				this.utils.err('okcoin err...',err);
			})
		},
		//测试iframe加载完成
		loadFun(){
			console.log('iframe load...');

			console.log('kline...contentWindow...',this.$refs.kline.contentWindow)
			console.log('kline...contentWindow...document',this.$refs.kline.contentWindow.document);
			var x = document.getElementById("kline");
			var y =  x.contentWindow || x.contentDocument;
			if (y.document)y=y.document;
			y.body.style.backgroundColor="#0000ff";
		}

		
	},
	beforeRouteEnter(to,from,next){
		console.log('to...',to);
		console.log('from...',from);
		next();
	}
}
